import React, { useCallback, useEffect } from 'react';
import s from './CreateFormPost.module.css';
import { useForm } from 'react-hook-form';
import api from '../../../utils/api';
import { Button, Paper, TextField, Typography } from '@mui/material';
import { Box } from '@mui/system';

const CreatePostForm = ({ handleClose, postData, onSave }) => {
  const {
    register,
    reset,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: 'onBlur',
    defaultValues: {
      title: postData?.title,
      text: postData?.text,
      image: postData?.image,
      tags: postData?.tags?.join(','),
    },
  });

  useEffect(() => {
    reset({
      title: postData?.title,
      text: postData?.text,
      image: postData?.image,
      tags: postData?.tags?.join(','),
    });
  }, [postData, reset]);

  const onSubmitHandler = useCallback(
    async (newData) => {
      console.log(newData);
      if (!postData) {
        return api.addNewPost(newData);
      }
      return api.changePost(postData._id, newData).then(() => {
        if (onSave) {
          onSave();
        }
      });
    },
    [postData, onSave]
  );

  const onSubmit = (data) => {
    onSubmitHandler({
      ...data,
      tags: data.tags
        .split(',')
        .map((word) => word.replace(/^\s+|\s+$/g, ''))
        .filter((word) => word !== ''),
    });
    handleClose(true);
  };

  return (
    <Paper
      sx={{
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        p: 4,
        pt: 3,
      }}
    >
      <form method="post" onSubmit={handleSubmit(onSubmit)}>
        <Typography variant="h5" align="center">
          {!postData ? ' ' : ' '}
        </Typography>
        <Box my={2}>
          <TextField
            type="text"
            size="small"
            fullWidth
            placeholder=""
            {...register('title', {
              required: '    ',
            })}
            error={errors?.title}
            helperText={errors?.title && errors.title.message}
          />
        </Box>
        <Box my={2}>
          <TextField
            type="text"
            size="small"
            fullWidth
            multiline
            maxRows={5}
            placeholder=""
            {...register('text', {
              required: '    ',
            })}
            error={errors?.text}
            helperText={errors?.text && errors.text.message}
          />
          {errors && errors.description ? (
            <div>
              <p className={s.errorMessage}>{errors.text.message}</p>
            </div>
          ) : null}
        </Box>
        <Box my={2}>
          <TextField
            type="text"
            size="small"
            fullWidth
            placeholder="  "
            {...register('image')}
          />
        </Box>
        <Box my={2}>
          <TextField
            type="text"
            size="small"
            fullWidth
            placeholder="   "
            {...register('tags')}
          />
        </Box>
        <Box my={2}>
          <Button type="submit" fullWidth color="primary" variant="contained">
            {postData ? '' : ''}&nbsp;
          </Button>
        </Box>
        <Box my={2}>
          <Button
            fullWidth
            color="error"
            variant="outlined"
            onClick={handleClose}
          >
            
          </Button>
        </Box>
      </form>
    </Paper>
  );
};

export default CreatePostForm;
