import React from "react";
import s from "./UserForm.module.css";
import { useForm } from "react-hook-form";

const UserForm = () => {

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: "onBlur" });
    const onSubmit = (data) => {
      console.log(data);
    };

  return (
    <form method="post" className={s.form} onSubmit={handleSubmit(onSubmit)}>
      <h3 className={s.title}></h3>
      <label>
        
        <input
          type="text"
          placeholder=" "
          {...register("name", {
            required: " ",
          })}
        />
        {errors && errors.name ? (
            <div>
                <p className={s.errorMessage}>{errors.name.message}</p>
            </div>
        ) : null}
      </label>
      <label>
         
        <textarea
          type="text"
          placeholder="    "
          {...register("about")}
        />
      </label>
      <input type="text" placeholder="  " {...register("photo")} />
      <div className={s.photo}>
        <img
          src="https://ic.pics.livejournal.com/olegmakarenko.ru/12791732/2404737/2404737_original.jpg"
          alt=" "
        />
      </div>
      <button className={s.btn}></button>
    </form>
  );
};

export default UserForm;
